import {
	mapState
} from 'vuex';
import uniNumberBox from '@/components/uni-number-box.vue'
import sunuiStepper from '@/components/sunui-stepper/sunui-stepper.vue'
export default {
	components: {
		uniNumberBox,
		sunuiStepper
	},
	data() {
		return {
			maskState: 0, //优惠券面板显示状态
			desc: '', //备注
			payType: 1, //1微信 2支付宝
			addressData: {},
			good_data: {},
			totalPrice: 0,
			good_dataCart: [],
			isFromCart: false,
			hasAddress:false,
		}
	},
	computed: {
		...mapState(['token'])
	},
	onLoad(option) {
		if (option.temp_good_data) {
			this.isFromCart = false;
			this.good_data = JSON.parse(option.temp_good_data);
			this.totalPrice = this.good_data.price;
		}
		if (option.data) {
			this.isFromCart = true;
			this.good_dataCart = JSON.parse(option.data).goodsData;
			this.setTotalPrice();
		}
	},
	onShow(){
		this.getUserAddress();
	},
	methods: {
		// addNewAddress() {
		// 	uni.navigateTo({
		// 		url: "/pages/address/addressManage?type=" + "add"
		// 	})
		// },
		getUserValue(e) {
		},

		stepperCart(e) {
			this.good_dataCart[e.index].nums = e.val;
			this.setTotalPrice();
		},
		stepper3(e) {
			this.good_data.count_num = e.val;
			this.setTotalPrice();
		},
		setTotalPrice() {
			if (this.isFromCart) {
				this.totalPrice = 0;
				for (let i = 0; i < this.good_dataCart.length; i++) {
					this.totalPrice += this.good_dataCart[i].goods.price * this.good_dataCart[i].nums
				}
			} else {
				this.totalPrice = this.good_data.price * this.good_data.count_num;
			}

		},
		getUserAddress() {
			var that=this;
			var now_token = this.token
			uni.request({
				url: that.global_baseUrl + '/user_address/',
				header: {
					'Authorization': ' Token ' + now_token
				},
				method: "GET",
				success: (res) => {
					that.check_statusCode(res.statusCode)
					try{
						var temp_address = {
							id: res.data[0].id,
							name: res.data[0].consignee_name,
							mobile: res.data[0].phone_num,
							address: res.data[0].consinfee_address,
							default: true,
							area: res.data[0].note
						}
						that.addressData = temp_address;
						if(that.addressData.id){
							that.hasAddress=true;
						}else{
							that.hasAddress=false;
						}
					}catch(e){
						that.hasAddress=false;
					}
					
					
				}
			});
		},
		changePayType(type) {
			this.payType = type;
		},
		submit() {
			if(this.hasAddress){
				var temp_data = {
					count_num: this.good_data.count_num,
					count_price: this.totalPrice,
					deliver_type: "null",
					deliver_com: "null",
					deliver_num: "null",
					user_note: this.desc,
					confirm_pic: null,
					address: {
						id:this.addressData.id,
						consignee_name:this.addressData.name,
						consinfee_address:this.addressData.address,
						note:this.addressData.area,
						phone_num: this.addressData.mobile
					}
				}
				if (this.isFromCart) {
					// 多商品
					let temp_count_num = 0
					for (let i = 0; i < this.good_dataCart.length; i++) {
						temp_count_num += this.good_dataCart[i].nums;
					}
					temp_data.count_num = temp_count_num;
					var temp_all_data = {
						isFromCart: true,
						first_data: temp_data,
						second_need_data: this.good_dataCart
					}
				} else {
					// 单条商品
					// 第一步请求的信息
					var temp_all_data = {
						isFromCart: false,
						first_data: temp_data,
						second_need_data: this.good_data
					}
				}
				uni.redirectTo({
					url: '/pages/money/pay?all_data=' + JSON.stringify(temp_all_data)
				})
				
			}else{
				uni.showModal({
					showCancel: false,
					title: '提示',
					content: '收货地址为空，请添加！',
					confirmText: '确定',
					success: function(res) {
						if (res.confirm) {
						
						}
					}
				});
			}
			
		},
		stopPrevent() {}
	}
}
